.chat {
  height: 100%;
  grid-template-columns: 310px 1fr;
  margin: 0;
  padding: 0;
  display: grid;
}

.chat__main {
  list-style: none;
}

.chat__main_left {
  text-align: right;
  background-color: #fbfbfb;
  border-right: 1px solid #eaeaea;
  overflow-y: auto;
}

.chat__main-dialog {
  margin: 10px 20px;
  overflow-y: auto;
}

.chat__footer {
  border-top: 1px solid #efefef;
}

.chat__footer-form {
  justify-content: space-between;
  align-items: center;
  font-family: inherit;
  display: flex;
}

.chat__footer-icon {
  height: 32px;
  width: 32px;
}

.chat__footer-btn-attach {
  cursor: pointer;
  width: 32px;
  height: 32px;
  background-color: #0000;
  border: none;
  padding: 0;
}

.chat__footer-btn-send {
  box-sizing: border-box;
  cursor: pointer;
  min-width: 30px;
  height: 30px;
  background-color: #2e69fd;
  border: none;
  border-radius: 50%;
  padding: 0;
  transform: scaleX(-1);
}

.chat__footer-input {
  box-sizing: border-box;
  width: 100%;
  color: #1e1e1e;
  background-color: #efefef;
  border-width: 1px;
  border-color: #0000;
  border-radius: 30px;
  outline: none;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 10px;
  font-weight: 500;
  line-height: 10px;
}

.chat__profile {
  margin-top: 20px;
  text-decoration: none;
  display: inline-block;
}

.chat__link-text {
  color: #999;
  margin-right: 8px;
  font-size: 10px;
  font-weight: 500;
  line-height: 10px;
}

.chat__inner {
  align-items: center;
  display: flex;
}

.chat__list {
  margin: 0;
  padding: 0;
}

.chat__title {
  color: #999;
  margin: 0;
  font-size: 10px;
  font-weight: 500;
  line-height: 10px;
}

.chat__header {
  border-bottom: 1px solid #e5e5e5;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  display: flex;
}

.chat__user-name {
  color: #1e1e1e;
  margin: 0;
  font-size: 15px;
  font-weight: 600;
  line-height: 15px;
}

